// Task.jsx
import React, { Component } from 'react';
import Tarefa from './screens/Tarefa';
import Introducao from './screens/Introducao';
import TaskData from './entities/TaskData';
import { Typography } from '@material-ui/core';

export default class Task extends Component {

    constructor(props) {
        super(props);

        let state = 'cadastro';

        this.state = { state, data:{} };
        this.handleUpdate = this.handleUpdate.bind(this);
        this.handleError = this.handleError.bind(this);
        window.handleError = this.handleError;
    }

    componentDidMount() {
        const debug = true;
        // Checking connectivity
        TaskData.checkServer()
            .catch( err => {
                if (!debug) window.handleError(err, '[E001] Servidor de dados não responde!')
            })
    }

    handleError = (err, msg) => {
        this.setState({
            state: 'error',
            err: msg
        });
    }

    handleUpdate(state, data) {
        // Updating screen, if necessary
        this.setState({ state, data });
    }

    render() {
        switch (this.state.state) {
            // Treino para a tarefa principal
            case 'cadastro':
                return (
                    <Introducao update={this.handleUpdate} />
                );

            // Tarefa principal
            case 'tarefa':
                return (
                    <React.Fragment>
                        <Tarefa
                            email={this.state.data.email}
                            nbox={window.qparsed.nbox}
                            update={this.handleUpdate}
                        />
                    </React.Fragment>
                );
            case 'error':
                return (
                    <React.Fragment>
                        <h2>Falha!</h2>
                        <p>Por favor, entre em contato com o responsável pela ferramenta e informe a mensagem de erro abaixo:</p>
                        <p style={{ color: 'red' }}>{this.state.err}</p>
                    </React.Fragment>
                );
            case 'fim':
                let acumulado = 0;
                return (
                    <React.Fragment>
                        <Typography component="h1" variant="h4" align="center">
                            Tarefa concluída!
                        </Typography>
                        <table border='1'>
                            <tbody>
                                <tr>
                                    <th>#</th>
                                    <th>HIT</th>
                                    <th>Distancia</th>
                                    <th>Score</th>
                                    <th>Acumulado</th>
                                </tr>
                                {this.state.data.answers.map((e, i) => {
                                    acumulado += e.score
                                    return (
                                        <tr key={i}>
                                            <td>{i + 1}</td>
                                            <td>{e.hit ? 'Yes' : 'No'}</td>
                                            <td>{e.distancia}</td>
                                            <td>{e.score}</td>
                                            <td>{acumulado}</td>
                                        </tr>
                                    )
                                })}
                            </tbody>
                        </table>
                    </React.Fragment>
                );
            default:
                return (<React.Fragment>Aguarde...</React.Fragment>);
        }
    }
}


